export default {
	data() {
		return {
			
		}
	},
	methods: {
		opennew:function(id){
			uni.navigateTo({
				url: '../'+id+'/'+id
			});
			
		},
		opennew02:function(id){
			uni.switchTab({
				url: '../'+id+'/'+id
			});
			
		},
		// 联系采购商
		tellphone:function(phone){
			uni.makePhoneCall({
			    phoneNumber: phone 
			});
		},
		// 展示求购单类型
		showtype:function(){
			uni.showActionSheet({
			    itemList: ['全部', '已过期', '审核中'],
			    success: function (res) {
			        console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		}
	}
}
